.widget {
  margin-bottom: 40px;
  padding: 40px;
  border: 1px solid #f3f3f3;

  .widget-title {
    font-size: 20px;
    margin-bottom: 28px;
    padding-right: 40px;
    position: relative;
    display: inline-block;
    color: $dark-color;
    font-family: $second-font-family;

    &::after {
      content: '';
      width: 27px;
      height: 3px;
      position: absolute;
      top: 50%;
      right: 0;
      -webkit-transform: translate(0, -50%);
      -khtml-transform: translate(0, -50%);
      -moz-transform: translate(0, -50%);
      -ms-transform: translate(0, -50%);
      -o-transform: translate(0, -50%);
      transform: translate(0, -50%);
      background-color: $secondary-color;
    }
  }

  .tagcloud a {
    border: none;
    background-color: #fff;
    padding: 0 21px;
    color: #3a4268;
    display: inline-block;
    font-size: 13px !important;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 30px;
    margin-right: 4px;
    margin-bottom: 13px;
    position: relative;
    z-index: 1;
    border: 1px solid #eeecec;
  }

  @include media-breakpoint-down(md) {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.widget_categories {
  ul {
    padding: 0;

    li {
      list-style: none;
      position: relative;
      border-bottom: 1px solid #f6f6f6;
      padding: 14px 40px 14px 0;
      position: relative;
      position: relative;
      z-index: 1;

      &:last-child {
        border-bottom: 0;
      }

      &::after {
        content: '\f105';
        font-family: "font awesome 6 Free";
        font-weight: 900;
        color: inherit;
        -webkit-transition: all 240ms linear 0ms;
        -khtml-transition: all 240ms linear 0ms;
        -moz-transition: all 240ms linear 0ms;
        -ms-transition: all 240ms linear 0ms;
        -o-transition: all 240ms linear 0ms;
        transition: all 240ms linear 0ms;
        position: absolute;
        top: 50%;
        right: 0;
        -webkit-transform: translate(0, -50%);
        -khtml-transform: translate(0, -50%);
        -moz-transform: translate(0, -50%);
        -ms-transform: translate(0, -50%);
        -o-transform: translate(0, -50%);
        transform: translate(0, -50%);
        font-size: 12px;
      }

      a {
        color: $dark-color;
        display: inline-block;
        font-size: 15px;
        font-weight: 500;
        line-height: normal;

        &:hover {
          color: $secondary-color;
        }
      }
    }
  }
}

.recent-posts-widget-with-thumbnails {
  ul {
    li {
      padding-bottom: 21px;
      margin-bottom: 24px;
      border-bottom: 1px solid #f6f6f6;

      &:last-child {
        margin-bottom: 0;
        padding-bottom: 0;
        border-bottom: none;
      }

      img {
        -webkit-border-radius: 100%;
        -khtml-border-radius: 100%;
        -moz-border-radius: 100%;
        -ms-border-radius: 100%;
        -o-border-radius: 100%;
        border-radius: 100%;
        width: 65px;
        height: 65px;
        object-fit: cover;
      }

      .rpwwt-post-title {
        font-size: 17px;
        line-height: 26px;
        margin-bottom: 8px;
        font-weight: 600;
        color: $dark-color;

        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        letter-spacing: 0;
        overflow: hidden;

        &:hover {
          color: $secondary-color;
        }
      }

      .rpwwt-post-date {
        font-size: 15px;
        color: #3a4268;

        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        letter-spacing: 0;
        overflow: hidden;
      }
    }
  }
}

.widget_search {
  .search-form {
    position: relative;

    .search-field {
      -webkit-border-radius: 0;
      -khtml-border-radius: 0;
      -moz-border-radius: 0;
      -ms-border-radius: 0;
      -o-border-radius: 0;
      border-radius: 0;
      background-color: #f4f7fc;
      border: none;
      padding: 0 19px;
      color: $dark-color;
      font-size: 15px;
      font-family: poppins, sans-serif;
      font-weight: 600;

      height: 50px;
      -webkit-transition: all 200ms linear 0ms;
      -khtml-transition: all 200ms linear 0ms;
      -moz-transition: all 200ms linear 0ms;
      -ms-transition: all 200ms linear 0ms;
      -o-transition: all 200ms linear 0ms;
      transition: all 200ms linear 0ms;
      -webkit-box-shadow: none;
      -khtml-box-shadow: none;
      -moz-box-shadow: none;
      -ms-box-shadow: none;
      -o-box-shadow: none;
      box-shadow: none;
      -webkit-border-radius: 0;
      -khtml-border-radius: 0;
      -moz-border-radius: 0;
      -ms-border-radius: 0;
      -o-border-radius: 0;
      border-radius: 0;
      width: 100%;
      outline: none;
    }

    .search-submit {
      padding: 0;
      border: none;
      line-height: 50px;
      height: 50px;
      width: 50px;
      position: absolute;
      top: 0;
      right: 0;
      color: #fff;
      font-size: 15px;
      text-indent: -999px;
      overflow: hidden;

      -webkit-border-radius: 0;
      -khtml-border-radius: 0;
      -moz-border-radius: 0;
      -ms-border-radius: 0;
      -o-border-radius: 0;
      border-radius: 0;
      -webkit-box-shadow: none;
      -khtml-box-shadow: none;
      -moz-box-shadow: none;
      -ms-box-shadow: none;
      -o-box-shadow: none;
      box-shadow: none;
      background-color: transparent;
      background-image: none;
    }

    span.screen-reader-text {
      position: absolute;
      clip: initial;
      clip-path: initial;
      top: 1px;
      right: 1px;
      background-color: $secondary-color;
      width: 50px;
      height: 50px;
      overflow: hidden;

      &::before {
        content: "\f002";
        font-weight: 900;
        font-family: "Font Awesome 6 Free";
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        font-style: normal;
        font-variant: normal;
        line-height: 1;
        text-rendering: auto;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 18px;
        color: $light-color;
      }
    }

    label {
      width: 100%;
    }
  }
}