@font-face {
  font-family: flaticon;
  src: url(../fonts/flaticon-v1/flaticon-v1.eot);
  src: url(../fonts/flaticon-v1/flaticon-v1.eot?#iefix)format("embedded-opentype"),
    url(../fonts/flaticon-v1/flaticon-v1.woff2)format("woff2"),
    url(../fonts/flaticon-v1/flaticon-v1.woff)format("woff"),
    url(../fonts/flaticon-v1/flaticon-v1.ttf)format("truetype"),
    url(../fonts/flaticon-v1/flaticon-v1.svg#flaticon-v1)format("svg");
  font-weight: 400;
  font-style: normal
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  @font-face {
    font-family: flaticon;
    src: url(../fonts/flaticon-v1/flaticon-v1.svg#flaticon-v1)format("svg")
  }
}

